import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';

@Component({
  selector: 'sof-search-bar',
  styleUrls: ['./search-bar.component.scss'],
  template: `
    <div class="input-group mb-3 sof-input-group">
      <div class="input-group-prepend">
        <span class="input-group-text" (click)="searchInput.focus()">
          <sof-svg-icon icon="icon-search" class="search-icon"></sof-svg-icon>
        </span>
      </div>
      <input
        #searchInput
        [(ngModel)]="internalValue"
        (keyup)="onKeyPress()"
        type="text"
        class="form-control"
        [class.clear-input]="internalValue"
        placeholder="{{ placeholder }}"
      />
      <div *ngIf="internalValue" class="input-group-append">
        <span class="input-group-text">
          <button class="btn btn-plain" (click)="onClear()">
            <sof-svg-icon
              icon="icon-cross"
              class="sof-clear-icon"
              size="8"
            ></sof-svg-icon>
          </button>
        </span>
      </div>
    </div>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [{ provide: SOF_FOCUS_COMPONENT, useExisting: SearchBarComponent }]
})
export class SearchBarComponent implements OnSofFocus {
  @Input() placeholder: string;
  @Output() search = new EventEmitter<string>();
  internalValue = '';

  @ViewChild('searchInput') searchInput: ElementRef;

  onClear(): void {
    this.internalValue = '';
    this.search.emit('');
  }

  onKeyPress(): void {
    this.search.emit(this.internalValue);
  }

  sofFocus(): void {
    this.searchInput.nativeElement.focus();
  }
}
